import { Message, MessageBox } from 'element-ui';

/**
 * 
 * @param {String} type      
 */
export function confirmBox(type = '', operate = '') {
    return new Promise((resolve, reject) => {
        MessageBox.confirm('' + operate + type + ', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(() => {
            resolve();
        }).catch(() => {
            Message({
                type: 'info',
                message: ''
            });
            // reject();
        });
    });
}


